/* ===========================
    Livia Notification CSS
=========================== */
/* Base */
/* =========================
   Base
   ======================= */
:root {
  --livia-container-width: 360px;
  --livia-icon-size: 2.4em;
  --livia-c-success: #01D28E;
  --livia-c-success-rgb: 1, 210, 142;
  --livia-c-success-lighter: hsl(160, 99%, 94%);
  --livia-c-success-darker: hsl(160, 99%, 36%);
  --livia-c-error: #FF595E;
  --livia-c-error-rgb: 255, 89, 94;
  --livia-c-error-lighter: hsl(359, 100%, 96%);
  --livia-c-error-darker: hsl(359, 100%, 65%);
  --livia-c-warning: #FFCA3A;
  --livia-c-warning-rgb: 255, 202, 58;
  --livia-c-warning-lighter: hsl(48, 100%, 96%);
  --livia-c-warning-darker: hsl(48, 100%, 65%);
  --livia-c-info: #1982C4;
  --livia-c-info-rgb: 25, 130, 196;
  --livia-c-info-lighter: hsl(204, 100%, 96%);
  --livia-c-info-darker: hsl(204, 100%, 65%);
  --livia-c-background: #fff;
  --livia-c-background-rgb: 255, 255, 255;
  --livia-c-background-darker: #f7f8f9;
}

.livia-container {
  position: absolute;
  top: 1.6em;
  left: 1.6em;
  font-size: 10px;
  display: grid;
  grid-template-columns: var(--livia-container-width);
  gap: 0.8em;
}

/* Themes */
/* =========================
   Default Theme
   ======================= */
.livia-notice {
  display: flex;
  align-items: center;
  max-width: 100%;
  background-color: var(--livia-c-background);
  box-shadow: 0 0.4em 1.2em -0.2em rgba(0, 0, 0, 0.1);
  border-radius: 1.4em;
}
.livia-notice.livia-notice__success {
  background-color: var(--livia-c-success-lighter);
  color: var(--livia-c-success);
}
.livia-notice.livia-notice__success .livia-notice__icon {
  color: var(--livia-c-success);
}
.livia-notice.livia-notice__success .livia-notice__content .livia-notice__title {
  color: var(--livia-c-success);
}
.livia-notice.livia-notice__error {
  background-color: var(--livia-c-error-lighter);
  color: var(--livia-c-error);
}
.livia-notice.livia-notice__error .livia-notice__icon {
  color: var(--livia-c-error);
}
.livia-notice.livia-notice__error .livia-notice__content .livia-notice__title {
  color: var(--livia-c-error);
}
.livia-notice.livia-notice__warning {
  background-color: var(--livia-c-warning-lighter);
  color: var(--livia-c-warning);
}
.livia-notice.livia-notice__warning .livia-notice__icon {
  color: var(--livia-c-warning);
}
.livia-notice.livia-notice__warning .livia-notice__content .livia-notice__title {
  color: var(--livia-c-warning);
}
.livia-notice.livia-notice__info {
  background-color: var(--livia-c-info-lighter);
  color: var(--livia-c-info);
}
.livia-notice.livia-notice__info .livia-notice__icon {
  color: var(--livia-c-info);
}
.livia-notice.livia-notice__info .livia-notice__content .livia-notice__title {
  color: var(--livia-c-info);
}
.livia-notice .livia-notice__icon {
  padding: 1.2em 0.8em 1.2em 1.6em;
}
.livia-notice .livia-notice__icon img {
  display: inline-block;
  width: var(--livia-icon-size);
  height: var(--livia-icon-size);
  vertical-align: middle;
  text-align: center;
  line-height: 1.6em;
}
.livia-notice .livia-notice__content {
  padding: 1.2em 1.6em 1.2em 0.8em;
  font-size: 1.4em;
  line-height: 1em;
  flex: 1;
  display: grid;
  gap: 0.4em;
}
.livia-notice .livia-notice__content .livia-notice__title {
  display: block;
  font-weight: 700;
}
.livia-notice .livia-notice__content .livia-notice__message {
  display: block;
  font-weight: 200;
}